/* Snapshots */

.rpc-name {
  font-size: 13px;
}

.col--rpc {
  width: 50%;
}

.col.col--rpc-logoname {
  display: flex;
  align-items: center;
}

.tb .td.td--rpc {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.copy-btn {
  background: #fff;
  border: 0;
  padding: 1px 6px;
  margin: 0 0 0 10px;
}

.button--rpc {
  border: 0;
  background-color: #67B9E7 !important;
  font-weight: bold !important;
}

.button.button--popup {
  background: #4A72FF !important;
  border: 0 !important;
  font-weight: bold !important;
}

.popup-content {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 10px 50px;
}

.swal2-confirm.popup-confirm {
  filter: drop-shadow(4px -2px 9px rgba(0, 0, 0, 0.25));
  background: #4A72FF !important;
  width: 105px;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  font-weight: 500;
  font-size: 13px;
  border-radius: 0 !important;
}

.swal2-confirm.popup-confirm:focus {
  border: 0;
}

.swal2-close.popup-close {
  position: absolute;
  color: #000;
  font-size: 1.5rem;
  top: 5px;
  right: 5px;
}

.logo-wrapper {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

/* Bots */

.td .button.button--withicon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 196px;
  margin-top: 0;
}

.button--withicon__img {
  display: block;
  margin-right: 10px;
}

/* Faucets */

.col--faucets {
  width: 50%;
  display: flex;
  align-items: center;
}

.holder {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.holder span {
  padding: 0 !important;
  width: 33% !important;
}

// Loader

.spinner {
  display: inline-block;
  margin-left: 10px;
  padding-top: 2px;
}

// Input time show animation

// .faucet-container {
//   position: relative;
// }

.row-01 {
  position: relative;
  background: #fff;
  z-index: 100;
}

.form-block {
  padding: 0 52px 0 68px;
}

.form-block,
.form-block *:not(.msg, .spinner *) {
  z-index: 0;
  transform-origin: top;
  transform: scaleY(0);
  top: -80px;
  height: 0px;
  opacity: 0;
  transition: all 0.3s ease;
}


.show {
  transform: scaleY(1);
  top: 0;
  opacity: 1;
  height: 80px;
  padding: 20px 52px 24px 68px;
  transition: all 0.3s ease;
}

.form-block.show *:not(.msg, .spinner *) {
  transform: scaleY(1);
  opacity: 1;
  top: 0;
  height: 35px;
  transition: all 0.3s ease;
}

// Message border animation

@mixin msg-before-base($width, $color, $duration, $direction) {
  content: '';
  position: absolute;
  border-radius: 5px;
  width:calc(100% + #{$width * 2});
  height:calc(100% + #{$width * 2});
  top:calc(#{$width}/-1);
  left:calc(#{$width}/-1);
  background: linear-gradient(to right, $color 0%, $color 100%), linear-gradient(to top, $color 50%, transparent 50%), linear-gradient(to top, $color 50%, transparent 50%), linear-gradient(to right, $color 0%, $color 100%), linear-gradient(to left, $color 0%, $color 100%);
  background-repeat: no-repeat, no-repeat;
}

$borderWidth: 2px;
$borderColor: #fff;
$animDuration: 1s;
$animDirection: 0;

.form-block .msg:before {
  @include msg-before-base($borderWidth, $borderColor, $animDuration, $animDirection);

  background-size: 200% $borderWidth, $borderWidth 400%, $borderWidth 400%, 55% $borderWidth, 55% $borderWidth;
  background-position: 50% 100%, 0% 100%, 100% 100%, 100% 0%, 0% 0%;
  transform: scaleX(1) rotate(180deg * $animDirection);
  transition-delay: 0s, $animDuration, $animDuration*2;

}

.form-block .msg.animated:before {
  @include msg-before-base($borderWidth, $borderColor, $animDuration, $animDirection);

  transition: transform $animDuration ease-in-out, background-position $animDuration ease-in-out, background-size $animDuration ease-in-out;

  background-size: 100% $borderWidth, $borderWidth 200%, $borderWidth 200%, 0% $borderWidth, 0% $borderWidth;
  background-position: 50% 100%, 0% 0%, 100% 0%, 100% 0%, 0% 0%;
  transform: scaleX(0) rotate(180deg * $animDirection);
  transition-delay: $animDuration*2, $animDuration, 0s;
}